import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
//TODO: –¥—ä–ª–≥–∏—è –∑–∞–ø–∏—Å –Ω–∞ ref={sortRef} —Ç–æ–≤–∞ –µ:
//ref={(elem) => {
//	sortRef.current = elem;
// }}
const SortPopup = React.memo(function SortPopup({ items, activeSortType, onClickSortType }) {
  const [showPopup, setShowPopup] = useState(false);

  //! –ó–∞–ø–∞–∑–≤–∞ —Ç–æ—á–Ω–∏—è –ø—ä—Ç –ø–æ –¥–æ–º –¥—ä—Ä–≤–æ—Ç–æ –≤ —Ç–æ–∑–∏ —Å–ª—É—á–∞–π —Ç–æ–≤–∞ –µ className=sort.
  const sortRef = useRef();
  const toggle = () => {
    setShowPopup(!showPopup);
  };
  //? –¢—É–∫ –ø—Ä–æ–≤–µ—Ä—è–≤–∞–º–µ –∫—ä–¥–µ —Ü—ä–∫–∞–º–µ –ø–æ —Å—Ç—Ä–∞–Ω–∏—Ü–∞—Ç–∞. –ê–∫–æ —Å–º–µ –∫–ª–∏–∫–Ω–∞–ª–∏ –Ω–∞ –ø–∞–¥–∞—â–∏—Ç–µ –º–µ–Ω—é—Ç–∞ —Ç–µ —Å–µ –ø–æ–∫–∞–∑–≤–∞—Ç. –ù–æ –∞–∫–æ —Å–º–µ –Ω—è–∫—ä–¥–µ –¥—Ä—É–≥–∞–¥–µ —Ç–µ —â–µ —Å–µ —Å–∫—Ä–∏–≤–∞—Ç —Å–∞–º–∏. –ó–∞—â–æ—Ç–æ e.path.includes(sortRef.current) –ø—Ä–æ–≤–µ—Ä—è–≤–∞ –∫—ä–¥–µ –∫–ª–∏–∫–∞–º–µ –ø–æ —Å—Ç—Ä–∞–Ω–∏—Ü–∞—Ç–∞ –∞–∫–æ –ø—ä—Ç—è —Å—ä–≤–ø–∞–¥–Ω–µ –ø–æ–∫–∞–∑–≤–∞ –ø–∞–¥–∞—â–∞—Ç–æ –º–µ–Ω—é –∏–Ω–∞—á–µ –≥–æ —Å–∫—Ä–∏–≤–∞.
  const handleOutsideClick = (e) => {
    const path = e.path || (e.composedPath && e.composedPath());
    if (!path.includes(sortRef.current)) {
      setShowPopup(false);
    }
  };
  //! –ó–∞–¥–∞ –º–æ–∂–µ –¥–∞ —Å–µ —Å–º–µ–Ω—è—Ç –º–µ–Ω—é—Ç–∞—Ç–∞ –∫–æ–π—Ç–æ –ø–∞–¥–∞—Ç–∞ —Å—ä–∑–¥–∞–º–µ –ø—Ä–æ–º–µ–Ω–ª–∏–≤–∞ –∏ –≤ –Ω–µ—è –∑–∞–ø–∏—Å–≤–∞–º–µ —Å—Ç–æ–π–Ω–æ—Å—Ç–∏—Ç–µ –æ—Ç –∫–æ–π—Ç–æ —Å–µ –¥—ä—Ä–∂–∞—Ç –≤ props.menu = –∏–º–µ–Ω–∞—Ç–∞ –Ω–∞ –ø–∞–¥–∞—â–∞–∏—Ç–µ –º–µ–Ω—é—Ç–∞ –∏ —Ç–µ –∑–∞–ø–æ—á–≤–∞—Ç –¥–∞ —Å–µ —Å–º–µ–Ω—è—Ç.

  const activeMenuName = items.find((obj) => obj.type === activeSortType).name;
  const onSelectItem = (index) => {
    if (onClickSortType) {
      onClickSortType(index);
    }

    setShowPopup(false);
  };

  useEffect(() => {
    document.body.addEventListener('click', handleOutsideClick);
  }, []);
  return (
    <div ref={sortRef} className='sort'>
      <div className='sort__label'>
        <svg
          className={showPopup ? 'rotated' : ''}
          width='10'
          height='6'
          viewBox='0 0 10 6'
          fill='none'
          xmlns='http://www.w3.org/2000/svg'>
          <path
            d='M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z'
            fill='#2C2C2C'
          />
        </svg>
        <b>Sort By:</b>
        <span onClick={toggle}>{activeMenuName}</span>
      </div>
      {showPopup && (
        <div className='sort__popup'>
          <ul>
            {items &&
              items.map((obj, index) => (
                <li
                  className={activeSortType === obj.type ? 'active' : ''}
                  onClick={() => onSelectItem(obj)}
                  key={`${obj.type}_${index}`}>
                  {obj.name}
                </li>
              ))}
            ;
          </ul>
        </div>
      )}
    </div>
  );
});

//! –ü—Ä–æ–≤–µ—Ä—è–≤–∞–º–µ  –∫–∞–∫–≤–∏ —Å–∞ —Ç–∏–ø–æ–≤–µ—Ç–µ –Ω–∞ –ø—Ä–æ–ø—Å.–¢–æ–≤–∞ –∫–æ–µ—Ç–æ –ø—Ä–∞–≤–∏ typeScript.–¢–æ–π –ø—Ä–æ–≤–µ—Ä—è–≤–∞ –≤ DB. –ò –∞–∫–æ —Ç–∞–º –Ω–µ—â–æ —Å–µ —Å–º–µ–Ω–∏ —Ö–≤—ä—Ä–ª—è –≥—Ä–µ—à–∫–∞ –≤ –∫–æ–Ω–∑–æ–ª–∞—Ç–∞ –Ω–∞ –±—Ä–∞—É–∑—ä—Ä–∞.
//?.arrayOf(PropTypes.number) - –ø—Ä–æ–≤–µ—Ä—è–≤–∞ –¥–∞ –ª–∏ –≤ –º–∞—Å–∏–≤–∞ –∏–º–∞ —Å–∞–º–æ —á–∏—Å–ª–∞ –∏ –∞–∫–æ –Ω—è–º–∞ —Ö–≤—ä—Ä–ª—è –≥—Ä–µ—à–∫–∞.
SortPopup.propTypes = {
  activeSortType: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  onClickSortType: PropTypes.func.isRequired,
};
////? –û–±–µ—Å–Ω—è–≤–∞–º  –Ω–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞,—á–µ –±–µ–∑ —Ç–µ–∑–∏ –¥–∞–Ω–Ω–∏ –Ω–µ–º–æ–∂–µ –¥–∞ –∂–∏–≤–µ–µ –∏ –∑–∞ –¥–∞–Ω—è–º–∞ –ø—Ä–æ–º–µ–±–ª–µ–º–∏ –¥–∞ —Å–µ —Ö–≤—ä—Ä–ª—è –≥—Ä–µ—à–∫–∞ –∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—Ç–æ –¥–∞ —Å–ø—Ä–µ –º—É –∑–∞–¥–∞–≤–∞–º –Ω—è–∫–∞–∫–≤–∏ –Ω–∞—á–∞–ª–Ω–∏ (defaultProps) —Å—Ç–æ–π–Ω–æ—Å—Ç–∏ –∑–∞ –ø—Ä–æ–ø—Å.–¢–æ–≤–∞ –≤—Å–∏—á–∫–æ –∞–∫–æ –Ω–µ—â–æ –ª–∏–ø—Å–≤–∞  –≤ DB.
SortPopup.defaultProps = { items: [] };
export default SortPopup;
