const addPizzCard = 'ADD_PIZZA_CARD';
const clearCart = 'CLEAR_CART';
const removeCartItem = 'REMOVE_CART_ITEM';
const onClickPlusCart = 'PLUS_CART_ITEM';
const onClickMinusCart = 'MINUS_CART_ITEM';
//?====================================================
const initialState = {
  items: {}, //! –≤–∑–µ–º–∞–º–µ –≤—Å–∏—á–∫–æ –∑–∞ –¥–∞–¥–µ–Ω–∞—Ç–∞ –ø–∏—Ü–∞ –∫–æ–µ—Ç–æ –µ –µ–¥–∏–Ω –æ–±–µ–∫—Ç —Å –¥–∞–Ω–Ω–∏ –∑–∞ –Ω–µ—è —Ü–µ–Ω–∞,—Ä–∞–∑–º–µ—Ä,–≤–∏–¥ –∏ —Å–ª–µ–¥ —Ç–æ–≤–∞ —â–µ –≥–∏ –¥–æ–±–∞–≤—è–º–µ –ø–æ id –≤ –∫–æ—à–Ω–∏—Ü–∞—Ç–∞ —Å–∞–º–æ —Ç–µ–∑–∏ –∫–æ–π—Ç–æ —Å–∏ –µ –∏–∑–±—Ä–∞–ª –ø–æ–ª–∑–≤–∞—Ç–µ–ª—è.–ù–∞–ø—Ä–∏–º–µ—Ä –∏–∑–±–∏—Ä–∞–º –ü–∏–ø–µ—Ä–æ–Ω–∏ 26—Å–º. —Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–∞ –≤  –∫–æ–ª–∏—á–∫–∞—Ç–∞ —Å–µ –¥–æ–±–∞–≤—è –Ω–µ–π–Ω–æ—Ç–æ ID –∏ —Ü–µ–Ω–∞ —Å–ª–µ–¥ —Ç–æ–≤–∞ –¥—Ä—É–≥–∞ –∏ –ø–∞–∫ —Å–µ –¥–æ–±–∞–≤—è ID –∏ —Ü–µ–Ω–∏—Ç–µ —Å–µ –Ω–∞—Ç—Ä—É–ø–≤–∞—Ç(—Å—ä–±–∏—Ä–∞—Ç –µ–¥–Ω–∞ —Å –¥—Ä—É–≥–∞) –∏ —Ç–∞–∫–∞ —â–µ —Å–µ –ø–æ–∫–∑–≤–∞ –±—Ä–æ—è –Ω–∞ –ø–∏—Ü–∏—Ç–µ –∫–æ–π—Ç–æ —Å—ä–º –∏–∑–±—Ä–∞–ª –∏ –∫–æ–ª–∫–æ –µ –º–∏ —Å—Ç—Ä—É–≤–∞—Ç.
  totalPrice: 0, //!–±—Ä–æ–π –ø–∏—Ü–∏
  totalCount: 0, //! —Å—É–º–∞—Ç–∞ –Ω–∞ –≤—Å–∏—á–∫–∏ –ø–∏—Ü–∏ –≤ –∫–æ–ª–∏—á–∫–∞—Ç–∞
};
//?====================================================

const getTotalPrice = (arr) => arr.reduce((sum, obj) => obj.price + sum, 0);
const _get = (obj, path) => {
  const [firstKey, ...keys] = path.split('.');
  return keys.reduce((val, key) => {
    return val[key];
  }, obj[firstKey]);
};

const getTotalSum = (obj, path) => {
  return Object.values(obj).reduce((sum, obj) => {
    const value = _get(obj, path);
    return sum + value;
  }, 0);
};
function Card(state = initialState, action) {
  switch (action.type) {
    case addPizzCard: {
      //–†–∞–∑–µ—Å–Ω—è–≤–∞–º —Ä–µ–¥–æ–≤–µ—Ç–µ: –ê–∫–æ –ª–∏—Å–ø—Å–≤–∞ –∏—Ç–µ–º–∞ —Å —Ç–∞–∫–æ–≤–∞ id —Å—ä–∑–¥–∞–π –≤ item –µ–¥–∏–Ω –º–∞—Å–∏–≤ –≤ –∫–æ–π—Ç–æ –∏–º–∞ –µ–¥–∏–Ω –æ–±–µ–∫—Ç.–ù–æ –∞–∫–æ –≥–æ –∏–º–∞ id —Ç–æ–≥–∞–≤–∞ —Å—ä–∑–¥–∞–π —Ç–∞–∫—ä–≤ –º–∞—Å–∏–≤ –∫–∞—Ç–æ –ø—ä—Ä–≤–æ –≤–∑–µ–º–µ—à –≤—Å–∏—á–∫–∏ —Å—Ç–∞—Ä–∏ –∑–Ω–∞—á–µ–Ω–∏—è –∏ –Ω–∞–∫—Ä–∞—è –¥–æ–±–∞–≤–∏ –Ω–æ–≤–∏—è –æ–±–µ–∫—Ç
      //[...state.items[action.payload.id].items - —Ç–æ–≤–∞ –æ–∑–Ω–∞—á–∞–≤–∞ –º–∏–Ω–∏ –ø–æ —Ü–µ–ª–∏—è –º–∞—Å–∏–≤
      const currentIdPizzaItem = !state.items[action.payload.id]
        ? [action.payload]
        : [...state.items[action.payload.id].items, action.payload];

      const newItems = {
        ...state.items,
        [action.payload.id]: {
          items: currentIdPizzaItem,
          totalPrice: getTotalPrice(currentIdPizzaItem),
        },
      };

      const totalCount = getTotalSum(newItems, 'items.length');
      const totalPrice = getTotalSum(newItems, 'totalPrice');
      //const items = Object.values(newItems).map((obj) => obj.items);
      //const allPizzas = [].concat.apply([], items);
      //const totalPrice = getTotalPrice(allPizzas);

      return {
        //TODO: –í–∑–µ–º–∞–º–µ —Å—Ç–∞—Ä–∏—è state —Å–ª–µ–¥—Ç–æ–≤–∞ items —Å—ä–∑–¥–∞–≤–∞–º–µ –Ω–æ–≤ –æ–±–µ–∫—Ç —Å–ª–µ–¥ —Ç–æ–≤–∞ id –∫–æ–µ—Ç–æ –Ω–∏–µ –µ –ø–æ–¥–∞–¥–µ–Ω–æ –æ—Ç —Ç–æ–≤–∞ –∫–æ—è –ø–∏—Ü–∞ —Å–º–µ –∏–∑–±—Ä–∞–ª–∏(–≤—Å—è–∫–∞ –ø–∏—Ü–∞ —Å–∏ –∏–º–∞ —Å–æ–±—Å—Ç–≤–µ–Ω–æ id) —Å–ª–µ–¥ —Ç–æ–≤–∞ –≤–∑–µ–º–∞–º–µ —Å—Ç–∞—Ä–∏—è –º–∞—Å–∏–≤ —Å –ø–∏—Ü–∏ –∏ —Ç—è—Ö–Ω–∏—Ç–µ  id –∏ —Å—ä–∑–¥–∞–≤–∞–º–µ –Ω–æ–≤ –º–∞—Å–∏–≤ –æ—Ç —Å—Ç–∞—Ä–∏—Ç–µ –æ–±–µ–∫—Ç–∏(—Ç–æ–≤–∞ —Å–∞ –ø–∏—Ü–∏—Ç–µ –∏ —Ç—è—Ö–Ω–∏—Ç–µ —Å–≤–æ–π—Å—Ç–≤–∞(—Ü–µ–Ω–∞,—Ä–∞–∑–º–µ—Ç ...) –∏ –∫—ä–º —Ç—è—Ö –Ω–∞–∫—Ä–∞—è –¥–æ–±–∞–≤—è–º–µ –Ω–æ–≤–æ—Ç–æ id  –Ω–∞ –Ω–æ–≤–∞—Ç–∞ –ø–∏—Ü–∞ –∫–æ—è—Ç–æ –µ –±–∏–ª–∞—Ç–∞ –∏–∑–±—Ä–∞–Ω–∞) –∏–∑–±–∏—Ä–∞–º –ø–∏—Ü–∞ —Å id=2 –≤ –∫–æ–ª–∏—á–∫–∞—Ç–∞ —Å–µ –¥–æ–±–∞–≤—è —Å–ª–µ–¥ —Ç–æ–≤–∞ –ø–∏—Ü–∞ —Å id=3 –≤ –∫–æ–ª–∏—á–∫–∞—Ç–∞ —Å–µ –¥–æ–±–∞–≤—è —Å—ä—â–æ –∏ —Ç—è –∏ —Ç–∞–∫–∞ –¥–æ–∫–∞—Ç–æ –Ω–µ —Å–µ –∑–∞–≤—ä—Ä—à–∏ –ø–æ—Ä—ä—á–∫–∞—Ç–∞.–ó–∞ –¥–∞ –º–æ–∂–µ –¥–∞ –Ω–µ —Ç–æ–ª–∫–æ–≤–∞ —Ç—Ä—É–¥–Ω–æ –∑–∞ —á–µ—Ç–µ–Ω–µ —Å–µ –∏–∑–ø–æ–ª–∑–≤–∞ –±–∏–±–ª–∏–æ—Ç–µ–∫–∞(YARN ADD IMMER  --SAVE)

        ...state,
        items: newItems,
        totalCount,
        //totalCount: allPizzas.length, //? –ü–æ–ª—É—á–∞–≤–∞–º–µ –º–∞—Å–∏–≤
        totalPrice,
      };
    }
    case clearCart:
      return {
        totalPrice: 0,
        totalCount: 0,
        items: {},
      };
    case removeCartItem: {
      const newItems = {
        ...state.items,
      };
      const currentTotalPrice = newItems[action.payload].totalPrice;
      const currentTotalCount = newItems[action.payload].items.length;
      delete newItems[action.payload];

      return {
        ...state,
        items: newItems,
        totalPrice: state.totalPrice - currentTotalPrice,
        totalCount: state.totalCount - currentTotalCount,
      };
    }
    case onClickPlusCart: {
      const newObjItems = [
        ...state.items[action.payload].items,
        state.items[action.payload].items[0],
      ];
      const newItems = {
        ...state.items,
        [action.payload]: {
          items: newObjItems,
          totalPrice: getTotalPrice(newObjItems),
        },
      };

      const totalCount = getTotalSum(newItems, 'items.length');
      const totalPrice = getTotalSum(newItems, 'totalPrice');

      return {
        ...state,
        items: newItems,
        totalCount,
        totalPrice,
      };
    }
    case onClickMinusCart: {
      const oldItems = state.items[action.payload].items;
      const newObjItems =
        oldItems.length > 1 ? state.items[action.payload].items.slice(1) : oldItems;
      const newItems = {
        ...state.items,
        [action.payload]: {
          items: newObjItems,
          totalPrice: getTotalPrice(newObjItems),
        },
      };

      const totalCount = getTotalSum(newItems, 'items.length');
      const totalPrice = getTotalSum(newItems, 'totalPrice');

      return {
        ...state,
        items: newItems,
        totalCount,
        totalPrice,
      };
    }

    default:
      return state;
  }
}

export default Card;
//totalPizzaPriceInCart = totalPrice;
