import React, { useState, useEffect } from 'react';

import PropTypes from 'prop-types';

import { FaBars, FaTimes } from 'react-icons/fa';

import { Navg } from './Element';
const Categories = React.memo(function Categories({
  activeCategory,
  items,
  onClickCategory,
  className,
  hambur,
}) {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const showButton = () => {
    if (window.innerWidth <= 768) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);
  window.addEventListener('resize', showButton);

  const handleClick = () => setClick(!click);
  //const [btcolor, setColor] = useState(false);
  //const toggle = () => {
  //  setColor(!btcolor);
  //};
  return (
    <main className='categories'>
      {/*<button className={btcolor ? 'bgr' : ''} onClick={toggle}>
        hi
      </button>*/}
      <div className='MobileIcon ' onClick={handleClick}>
        {click ? <FaTimes /> : <FaBars />}
      </div>
      <Navg onClick={handleClick} click={click}>
        <li
          className={activeCategory === null ? 'active' : ''}
          onClick={() => onClickCategory(null)}>
          All
        </li>

        {items &&
          items.map((e, index) => (
            <li
              className={activeCategory === index ? 'active' : ''}
              onClick={() => onClickCategory(index)}
              key={`${e}_${index}`}>
              {e}
            </li>
          ))}
      </Navg>
    </main>
  );
});

//! –ü—Ä–æ–≤–µ—Ä—è–≤–∞–º–µ  –∫–∞–∫–≤–∏ —Å–∞ —Ç–∏–ø–æ–≤–µ—Ç–µ –Ω–∞ –ø—Ä–æ–ø—Å.–¢–æ–≤–∞ –∫–æ–µ—Ç–æ –ø—Ä–∞–≤–∏ typeScript.–¢–æ–π –ø—Ä–æ–≤–µ—Ä—è–≤–∞ –≤ DB. –ò –∞–∫–æ —Ç–∞–º –Ω–µ—â–æ —Å–µ —Å–º–µ–Ω–∏ —Ö–≤—ä—Ä–ª—è –≥—Ä–µ—à–∫–∞ –≤ –∫–æ–Ω–∑–æ–ª–∞—Ç–∞ –Ω–∞ –±—Ä–∞—É–∑—ä—Ä–∞.
//?.arrayOf(PropTypes.number) - –ø—Ä–æ–≤–µ—Ä—è–≤–∞ –¥–∞ –ª–∏ –≤ –º–∞—Å–∏–≤–∞ –∏–º–∞ —Å–∞–º–æ —á–∏—Å–ª–∞ –∏ –∞–∫–æ –Ω—è–º–∞ —Ö–≤—ä—Ä–ª—è –≥—Ä–µ—à–∫–∞.
Categories.propTypes = {
  //activeCategory: PropTypes.oneOf([PropTypes.number, null]),
  items: PropTypes.arrayOf(PropTypes.string).isRequired,
  onClickCategory: PropTypes.func.isRequired,
};
////? –û–±–µ—Å–Ω—è–≤–∞–º  –Ω–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞,—á–µ –±–µ–∑ —Ç–µ–∑–∏ –¥–∞–Ω–Ω–∏ –Ω–µ–º–æ–∂–µ –¥–∞ –∂–∏–≤–µ–µ –∏ –∑–∞ –¥–∞–Ω—è–º–∞ –ø—Ä–æ–º–µ–±–ª–µ–º–∏ –¥–∞ —Å–µ —Ö–≤—ä—Ä–ª—è –≥—Ä–µ—à–∫–∞ –∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—Ç–æ –¥–∞ —Å–ø—Ä–µ –º—É –∑–∞–¥–∞–≤–∞–º –Ω—è–∫–∞–∫–≤–∏ –Ω–∞—á–∞–ª–Ω–∏ (defaultProps) —Å—Ç–æ–π–Ω–æ—Å—Ç–∏ –∑–∞ –ø—Ä–æ–ø—Å.–¢–æ–≤–∞ –≤—Å–∏—á–∫–æ –∞–∫–æ –Ω–µ—â–æ –ª–∏–ø—Å–≤–∞  –≤ DB.
Categories.defaultProps = { activeCategory: null, items: [] };

export default Categories;
